.Loader {
  background: #29335c;
  width: 100%;
  height: 100vh;
  position: fixed;
  top: 0;
  left: 0;
  z-index: 999;
  -webkit-transition: all 0.5s ease;
  transition: all 0.5s ease; }
  .Loader__svg {
    margin: 10px;
    position: absolute;
    top: 50%;
    left: 50%;
    -webkit-transform: translate(-50%, -50%);
            transform: translate(-50%, -50%); }
    .Loader__svg__line {
      stroke-width: 4;
      stroke-dashoffset: 500; }

.l1 {
  stroke: #e6e4e4;
  stroke-dasharray: 50,40;
  -webkit-animation: lolilol 10s linear infinite alternate;
          animation: lolilol 10s linear infinite alternate; }

.l2 {
  stroke: #008148;
  stroke-dasharray: 50,30;
  animation: lolilol 10s linear infinite alternate-reverse; }

.l3 {
  stroke: #f3a712;
  stroke-dasharray: 50,20;
  -webkit-animation: lolilol 10s linear infinite alternate;
          animation: lolilol 10s linear infinite alternate; }

.l4 {
  stroke: #db2b39;
  stroke-dasharray: 50,10;
  animation: lolilol 10s linear infinite alternate-reverse; }

@-webkit-keyframes lolilol {
  to {
    stroke-dashoffset: -500; } }

@keyframes lolilol {
  to {
    stroke-dashoffset: -500; } }

.brand {
  display: flex;
  align-items: center;
  cursor: pointer; }

.brand__image {
  width: 50px;
  height: 50px;
  margin-right: 10px; }

.brand__title {
  font-size: 24px;
  font-weight: 500;
  color: #fafafa; }

.filter {
  position: absolute;
  top: 27px;
  right: 20px;
  width: 140px;
  box-shadow: -1px 4px 18px 0 rgba(0, 0, 0, 0.2); }
  .filter:after {
    position: absolute;
    top: 9px;
    right: 16px;
    display: inline-block;
    content: '\25BE';
    font-size: 18px;
    color: #29335c; }
  .filter--active:after {
    top: 11px;
    -webkit-transform: rotate(180deg);
            transform: rotate(180deg); }

.filter__button {
  width: 100%;
  padding: 10px 16px;
  font-size: 13px;
  text-align: left;
  color: #29335c;
  background: #e6e4e4;
  border-radius: 3px;
  cursor: pointer; }

.filter--active .filter__button {
  border-radius: 3px 3px 0px 0px;
  box-shadow: 4px 0px 24px 0 rgba(0, 0, 0, 0.1); }

.filter__items {
  display: none;
  width: 100%;
  border-top: none;
  border-radius: 0px 0px 3px 3px;
  background: #d3d3d3; }

.filter--active .filter__items {
  display: block; }

.filter__item {
  padding: 10px 16px;
  font-size: 13px;
  color: #29335c;
  cursor: pointer;
  text-transform: capitalize; }
  .filter__item--active {
    color: #db2b39; }

.footer {
  position: fixed;
  right: 0px;
  bottom: 0px;
  left: 0px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 20px; }

.footer__item {
  font-size: 12px;
  color: #ffffff; }

.Header {
  position: fixed;
  top: 0px;
  right: 0px;
  left: 0px;
  z-index: 10;
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 20px; }

.menuBoxHidden {
  display: none; }

.tiles {
  margin: 0px auto; }

.tile {
