  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  width: calc(100vw - (40px * 2));
  max-width: 450px;
  height: 400px;
  background: #e6e4e4;
  border-radius: 20px;
  box-shadow: -1px 4px 18px 0 rgba(0, 0, 0, 0.5); }
  .tile--category {
    cursor: pointer; }
  .tile--result {
    cursor: pointer; }

.tile__header {
  position: absolute;
  top: 20px;
  right: 20px;
  left: 20px;
  display: flex;
  justify-content: space-between;
  align-items: center; }

.tile__counter {
